import { Box, Fade, Slide, Tab, Tabs } from "@mui/material";
import { grey } from "@mui/material/colors";
import Grid from "@mui/material/Unstable_Grid2";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

const Profile = ({ user, karakter, balance, transition }) => {
    const [tabValue, setTabValue] = useState(1);
    const navigate = useNavigate();
    const telegram = window.Telegram.WebApp;

    telegram.BackButton.show();
    telegram.BackButton.onClick(() => {
        navigate("/click-game");
        telegram.BackButton.hide();
    });

    return (
        <Box padding={1}>
            <Slide in={transition}>
                <Grid
                    sx={{
                        backgroundColor: "#303030",
                        borderRadius: "8px",
                        padding: 1.2,
                    }}
                    container="true"
                >
                    <Grid xs={3.5} alignItems={"center"} display={"flex"}>
                        <img
                            height={"90rem"}
                            style={{
                                border: "1px solid " + grey[800],
                                borderRadius: "5%",
                            }}
                            src={
                                "/assets/images/characters/" +
                                user.selfKarakter.id +
                                ".jpg"
                            }
                            alt=""
                        />
                    </Grid>
                    <Grid xs={7.5}>
                        <div style={{ fontSize: "28px" }}>{user.name}</div>
                        <div
                            style={{
                                fontSize: "18px",
                                marginTop: "-5px",
                                letterSpacing: "2px",
                            }}
                        >
                            {user.selfKarakter.name}
                        </div>
                        <div
                            style={{
                                fontSize: "18px",
                                marginTop: "-5px",
                                display: "flex",
                                alignItems: "center",
                            }}
                        >
                            <img
                                src="/assets/images/logo1.png"
                                height={"25rem"}
                                style={{ marginLeft: "-6px" }}
                                alt=""
                            />
                            <span
                                style={{
                                    marginLeft: "6px",
                                    letterSpacing: "1px",
                                }}
                            >
                                {balance.toLocaleString()}
                            </span>
                        </div>
                    </Grid>
                </Grid>
            </Slide>
            <Fade in={transition}>
                <Grid
                    sx={{
                        backgroundColor: "#303030",
                        borderRadius: "8px",
                        padding: 1.2,
                        marginTop: 1.3,
                    }}
                    container="true"
                >
                    <Grid xs={12}>
                        <span
                            style={{
                                justifyContent: "center",
                                display: "flex",
                                fontSize: "30px",
                                letterSpacing: "2px",
                            }}
                        >
                            Statistics
                        </span>
                    </Grid>
                    <Grid xs={12}>
                        <Grid container="true" gap={1}>
                            <Grid
                                xs={12}
                                border={"1px solid " + grey[700]}
                                borderRadius={"6px"}
                                padding={1}
                                letterSpacing={"1px"}
                                width={"100%"}
                            >
                                <span
                                    style={{
                                        fontSize: "18px",
                                        color: grey[100],
                                    }}
                                >
                                    level:{" "}
                                </span>
                                <span style={{ fontSize: "25px" }}>
                                    {karakter.level}
                                </span>
                            </Grid>
                            <Grid
                                xs={12}
                                border={"1px solid " + grey[700]}
                                borderRadius={"6px"}
                                padding={1}
                                letterSpacing={"1px"}
                                width={"100%"}
                            >
                                <span
                                    style={{
                                        fontSize: "18px",
                                        color: grey[100],
                                    }}
                                >
                                    Type:{" "}
                                </span>
                                <span style={{ fontSize: "25px" }}>
                                    {user.selfKarakter.type}
                                </span>
                            </Grid>
                            <Grid
                                xs={12}
                                border={"1px solid " + grey[700]}
                                borderRadius={"6px"}
                                padding={1}
                                letterSpacing={"1px"}
                                width={"100%"}
                            >
                                <span
                                    style={{
                                        fontSize: "18px",
                                        color: grey[100],
                                    }}
                                >
                                    Balance:{" "}
                                </span>
                                <span style={{ fontSize: "25px" }}>
                                    {balance.toLocaleString()}
                                </span>
                            </Grid>
                            <Grid
                                xs={12}
                                border={"1px solid " + grey[700]}
                                borderRadius={"6px"}
                                padding={1}
                                letterSpacing={"1px"}
                                width={"100%"}
                            >
                                <span
                                    style={{
                                        fontSize: "18px",
                                        color: grey[100],
                                    }}
                                >
                                    Invited friends:{" "}
                                </span>
                                <span style={{ fontSize: "25px" }}>
                                    {user.friends.length}
                                </span>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Fade>
            <Slide in={transition} direction="up">
                <Tabs
                    value={tabValue}
                    variant="fullWidth"
                    textColor=""
                    TabIndicatorProps={{
                        hidden: true,
                    }}
                    sx={{ marginTop: "10px" }}
                    onChange={(value) => {
                        setTabValue(value);
                    }}
                >
                    <Tab
                        label="Wallet"
                        disabled
                        sx={{
                            borderBottom: "2px solid " + grey[700],
                            color: grey[400],
                            textDecoration: "line-through",
                        }}
                    />
                    <Tab
                        label="League"
                        disabled
                        sx={{
                            borderBottom: "2px solid " + grey[700],
                            color: grey[400],
                            textDecoration: "line-through",
                        }}
                    />
                    <Tab
                        label="Clan"
                        disabled
                        sx={{
                            borderBottom: "2px solid " + grey[700],
                            color: grey[400],
                            textDecoration: "line-through",
                        }}
                    />
                </Tabs>
            </Slide>
        </Box>
    );
};

export default Profile;
